"use strict";

var Constants = require('./constants');

var Actions = {
  media: {
    select: function(media) {
      this.dispatch(Constants.MEDIA_SELECTED, {media:media});
    },

    fetch: function(category, filters) {
      this.dispatch(Constants.FETCH_IMAGES, {category:category, filters:filters});
    },

    fetchSuccess: function(response, categoryPath) {
      var data = response.body;
      var request = response.req;
      this.dispatch(Constants.FETCH_IMAGES_SUCCESS, {data:data, request:request, categoryPath:categoryPath});
    },

    fetchError: function(response) {
      console.log(response);
    }
  },

  crop: {
    select: function(crop, media) {
      this.dispatch(Constants.CROP_SELECTED, {crop:crop, media:media});
    },

    deselect: function(crop, media) {
      this.dispatch(Constants.CROP_DESELECTED, {crop:crop, media:media});
    },

    move: function(crop, dX, dY) {
      this.dispatch(Constants.CROP_MOVE, {crop:crop, dX:dX, dY:dY});
    },

    resize: function(crop, dX, dY, position) {
      this.dispatch(Constants.CROP_RESIZE, {crop:crop, dX:dX, dY:dY, position:position});
    },
  },

  categories: {
    select: function(category) {
      this.dispatch(Constants.CATEGORY_SELECTED, {category:category});
    },

    open: function(category) {
      this.dispatch(Constants.CATEGORY_OPEN, {category:category});
    },

    close: function(category) {
      this.dispatch(Constants.CATEGORY_CLOSE, {category:category});
    },    

    fetchChildrenSuccess: function(response) {
      var data = response.body;
      var request = response.req;
      this.dispatch(Constants.FETCH_CATEGORY_CHILDREN_SUCCESS, {data:data, request:request});
    },

    fetchChildrenError: function(response) {
      console.log(response);
    },

    loadChildren: function(category) {
      this.dispatch(Constants.CATEGORY_LOAD_CHILDREN, {category:category});
    }
  },

  uploads: {
    add: function(file, category) {
      this.dispatch(Constants.UPLOAD_ADD, {file:file, category:category});
    },

    progress: function(event, id, file, categoryPath) {
      this.dispatch(Constants.UPLOAD_PROGRESS, {event:event, id:id, file:file, categoryPath:categoryPath});
    },

    load: function(event, id, file, categoryPath) {
      this.dispatch(Constants.UPLOAD_LOAD, {event:event, id:id, file:file, categoryPath:categoryPath});
    },

    complete: function(response, id, file, categoryPath) {
      this.dispatch(Constants.UPLOAD_COMPLETE, {response:response, id:id, file:file, categoryPath:categoryPath});      
    }
  }
};

module.exports = Actions;


